import {CompactPicker, CirclePicker} from 'react-color';
import React, {Component} from 'react';
import * as R from 'ramda';

export default class ColorTheme extends Component {
    constructor() {
        super();
        this.state = {};
    }

    render() {
        const {theme, setTheme} = this.props;
        const {open} = this.state;
        return (
            <div>
                {R.keys(theme).map(color => {
                    const varColor = R.replace(/-/g, ' ', color);
                    return (
                    <div>
                        <div
                            style={{
                                'cursor': 'pointer',
                                'width': '100%',
                                'marginBottom': 20
                            }}
                        >
                            <b>{color}</b>
                            <div>
                                <div
                                    style={{
                                        'backgroundColor': theme[color],
                                        'width': '30px',
                                        'height': '30px',
                                        'display': 'inline',
                                        'float': 'left',
                                        'borderRadius': 2
                                    }}
                                    onClick={() => this.setState({
                                        open: open === color ? '' : color
                                    })}
                                />
                                <ControlledInput
                                    type="text"
                                    value={theme[color]}
                                    style={{
                                        'height': '30px'
                                    }}
                                    onBlur={newColor => setTheme(
                                        R.set(R.lensProp(color), newColor, theme)
                                    )}
                                />
                            </div>
                        </div>

                        {!Boolean(open === color) ? null :
                        <div style={{
                            'position': 'absolute',
                            'zIndex': '2',
                            'backgroundColor': 'var(--background)',
                            'padding': 5
                        }}>
                            <CompactPicker
                                onChange={newColor => setTheme(
                                    R.set(R.lensProp(color), newColor.hex, theme)
                                )}
                            />
                        </div>}


                    </div>
                );})}
            </div>
        );
    }
}

class ControlledInput extends Component {
    constructor() {
        super()
        this.state = {}
    }
    componentWillMount() {
        this.setState({value: this.props.value})
    }
    render() {
        const {value} = this.state;
        const {onBlur, style} = this.props;
        return <input
            type="text"
            value={value}
            onChange={e => this.setState({value: e.target.value})}
            onBlur={e => onBlur(e.target.value)}
            style={style}
        />
    }
}



