import React from 'react';
import PropTypes from 'prop-types';


const iconCategories = [
    // Always free
    'solid',
    // Sometimes free
    'regular',
    // Pro only
    'light'
];

/**
 * Font-awesome icons for dash apps.
 */
export default class Icon extends React.Component {
    componentWillMount() {
        // Insert the stylesheet or
        const { fa_script } = this.props;
        const faId = 'font-awesome';
        let element = document.getElementById(faId);
        if (element) {
            return;
        }

        if (fa_script) {
            let element = document.getElementById(faId);
            if (!element) {
                element = document.createElement('script');
                element.src = fa_script;
                const body = document.querySelector('body');
                body.appendChild(element);
            }
            return
        }

        if (!element) {
            element = document.createElement('link');
            element.rel = 'stylesheet';
            element.integrity = "sha384-mzrmE5qonljUremFsqc01SB46JvROS7bZs3IO2EmfFsd15uHvIt+Y8vEf7N7fWAU";
            element.crossOrigin = 'anonymous';
            element.href = 'https://use.fontawesome.com/releases/v5.3.1/css/all.css';
            element.id = faId;
            const head = document.querySelector('head');
            head.appendChild(element);
        }
    }

    render() {
        const { id, icon_name, icon_category, icon_color, style } = this.props;
        let { className } = this.props;
        className = className || '';
        // noinspection CheckTagEmptyBody
        const category =  icon_category
            ? iconCategories[iconCategories.indexOf(icon_category)][0]
            : iconCategories[0][0];
        return <i
            id={id}
            className={`fa${category} fa-${icon_name} ${className}`}
            style={{...style, color: icon_color || 'inherit'}}>
        </i>
    }
};

Icon.defaultProps = {};

Icon.propTypes = {
    id: PropTypes.string,

    /**
     * A class name to append to the font-awesome classes.
     */
    className: PropTypes.string,

    /**
     * The name of the font-awesome icon without the fa-
     */
    icon_name: PropTypes.string,

    /**
     * The category of the icon
     * One of
     * - solid      (always free)
     * - regular    (sometimes free)
     * - light      (pro-only)
     */
    icon_category: PropTypes.oneOf(iconCategories),

    /**
     * The color to apply to the icon.
     */
    icon_color: PropTypes.string,

    /**
     * Additional style object.
     */
    style: PropTypes.object,

    /**
     * Link to the fa script to enable pro icons.
     */
    fa_script: PropTypes.string,

    /**
     * Dash-assigned callback that gets fired when the value changes.
     */
    setProps: PropTypes.any
};



