import * as R from 'ramda';
import chroma from 'chroma-js';

const DATA_KEYS = [
    'x', 'y', 'z',
    'r', 'theta',
    'size',
    'lat', 'lon',
    'a', 'b', 'c'
];


/*
 * Abstract function to crawl a figure and call a function on the crawlled keys.
 * `skipKeys` allows the crawler to skip certain keys, like data keys,
 * that are unecessary
 */
function crawl(figureObj, applyFunctionOnKey, skipKeys) {
    if (R.type(figureObj) === 'Object') {
        R.keys(figureObj).forEach(key => {
            if (R.contains(key, skipKeys)) {
                return;
            }
            applyFunctionOnKey(figureObj, key);
            crawl(figureObj[key], applyFunctionOnKey, skipKeys);
        });
    } else if (R.type(figureObj) === 'Array') {
        figureObj.forEach((_, i) => {
            applyFunctionOnKey(figureObj, i);
            crawl(figureObj[i], applyFunctionOnKey, skipKeys);
        });
    }
}


/*
 * Clone the figure but skip the DATA_KEYS, so that we aren't
 * copying huge arrays (which could be slow)
 */
export function cloneFigureWithoutDataKeys(figure) {
    const newFigure = Object.assign({}, figure);
    crawl(
        newFigure,
        (parent, item) => {
            if (R.type(parent[item]) === 'Object') {
                parent[item] = Object.assign({}, parent[item]);
            } else if (R.type(parent[item]) === 'Array') {
                parent[item] = parent[item].slice(0);
            }
        },
        DATA_KEYS
    );
    return newFigure;
}

export function themeGraph(figure) {
    if (!window.dashTheme) {
        console.warn('window.dashTheme is not defined');
        return;
    }

    crawl(
        figure,
        (parent, item) => {
            if (R.type(parent[item]) === 'String' &&
                    R.startsWith('var(--', parent[item])) {
                const themeVariable = R.replace(')', '', R.replace(
                    'var(--', '', parent[item]
                ));
                if (R.startsWith('trace', themeVariable)) {
                    const traces = window.dashTheme.traces;
                    parent[item] = traces[R.match(/\d+/, themeVariable) % traces.length]
                } else {
                    parent[item] = window.dashTheme[themeVariable]
                }
            }
        },
        DATA_KEYS
    );

    const gridcolor = window.dashTheme.border;
    const bgcolor = window.dashTheme.background;
    const colorway = window.dashTheme.traces;
    const textColor = window.dashTheme.text;
    const font = window.dashTheme.font;



    const axis = () => ({
        'gridcolor': gridcolor,
        'zerolinecolor': gridcolor,
        'tickcolor': gridcolor,
        'linecolor': gridcolor,
        'automargin': true,
        'ticks': ''
    });

    const colorscaleProps = {
        colorscale: R.zip(
            R.map(R.divide(R.__, colorway.length-1), R.range(0, colorway.length+1)),
            colorway
        ),
        autocolorscale: false,
        colorbar: {
            ypad: 0,
            outlinewidth: 0
        }
    };

    if (!R.has('layout', figure)) {
        figure.layout = {};
    }
    figure.layout.template = {
        'data': {
            'scatter': [{
                opacity: 0.8,
                marker: R.merge(colorscaleProps, {size: 8})
            }],
            'bar': [{}],

            'heatmap': [colorscaleProps],
            'contour': [colorscaleProps],
            'histogram2d': [colorscaleProps],
            'histogram2dcontour': [colorscaleProps],

            'pie': [{}],

            'box': [{}],
            'violin': [{}],
            'histogram': [{}],

            'scatter3d': [{marker: colorscaleProps}],
            'surface': [colorscaleProps],
            'cone': [{}],
            'streamtube': [{}],

            'choropleth': [colorscaleProps],
            'scattergeo': [{marker: colorscaleProps}],
            'scattermapbox': [{marker: colorscaleProps}],

            'candlestick': [{
                'decreasing': {
                    'line': {'color': colorway[0]}
                },
                'increasing': {
                    'line': {'color': colorway[1]}
                },
            }],
            'ohlc': [{
                'decreasing': {
                    'line': {'color': colorway[0]}
                },
                'increasing': {
                    'line': {'color': colorway[1]}
                },
            }],

            'scatterternary': [{marker: colorscaleProps}],
            'scatterpolar': [{marker: colorscaleProps}],

            'parcoords': [{}],
            'sankey': [{}],

            'carpet': [{
                'aaxis': R.merge(
                    axis(), {
                        'minorgridcolor': gridcolor
                    }
                ),
                'baxis': R.merge(
                    axis(), {
                        'minorgridcolor': gridcolor,
                    }
                ),
                'font': {
                    'color': textColor,
                    'family': font
                }
            }],
            'contourcarpet': [colorscaleProps]
        },
        'layout': {
            'colorway': colorway,
            'paper_bgcolor': bgcolor,
            'plot_bgcolor': bgcolor,

            // margin should autoexpand to just contain the text
            'margin': {'l': 0, 'r': 10, 'b': 30, 't': 10},

            'legend': {
                'x': 1,
                'xanchor': 'right',
                'y': 1,
                'yanchor': 'top',
                'orientation': 'h',
                'bgcolor': chroma(bgcolor).alpha(0.75).css()
            },

            'hoverlabel': {
                'bgcolor': bgcolor,
                'bordercolor': gridcolor,
                'font': {
                    'color': textColor
                }
            },

            'font': {
                'color': textColor,
                'family': font
            },

            'xaxis': axis(),
            'yaxis': axis(),
            'scene': {
                'xaxis': axis(),
                'yaxis': axis(),
                'zaxis': axis(),
            },
            'polar': {
                'radialaxis': axis(),
                'angularaxis': axis(),
                'bgcolor': bgcolor
            },
            'ternary': {
                'aaxis': axis(),
                'baxis': axis(),
                'caxis': axis(),
                'bgcolor': bgcolor
            },
            'geo': {
                'lataxis': axis(),
                'lonaxis': axis(),
                'bgcolor': bgcolor
            },
            'mapbox': {
                'style': chroma(bgcolor).luminance() > 0.3 ? 'light' : 'dark'
            }
        }
    }
}



