import { css } from '../utils.js';
import * as R          from 'ramda';

/* 
	NOTE: Override css.js files prefix all selectors with `&&`.
	`styled-components` will replace `&` with the randomly-generated
	classname of the `<DashThemeProvider>` component.
	This is a hack that enforces specificity.
	See:
	https://github.com/styled-components/styled-components/issues/1253#issuecomment-337871693
	and
	https://css-tricks.com/the-sass-ampersand/#article-header-id-11
*/

const themedStyles = css`
	&& .Select-input > input,
	&& .Select-input > input:not([type]) {
	  background-color: ${props => props.theme.background};
	  border-color: none;
	  border-width: 0;
	  border-style: none;
	  border-radius: 0;
	}

	&& .Select-arrow {
		border-top-color    : ${props => props.theme.border};
		border-left-color   : transparent;
		border-right-color  : transparent;
		border-bottom-color : transparent;
	}

	&& .is-open > .Select-control .Select-arrow {
		border-bottom-color: ${props => props.theme.border};
		border-left-color: transparent;
		border-right-color: transparent;
		border-top-color: transparent;
	}

	&& .Select--multi .Select-value,
	.Select--multi a.Select-value-label {
		color: ${props => props.theme.text};
	}

	&& .Select-control { ${props => objectToStyles(props.theme.border_style)} }

	&& .Select.is-disabled > .Select-control {
		border: none;
	}

	&& .Select-control:hover {
		border-color: ${props => props.theme.border};
		cursor: pointer;
	}

	&& .Select--multi .Select-value-icon:hover, .Select--multi .Select-value-icon:focus {
		font-weight: bold;
	}

	&& .Select.is-disabled .Select-control {
		cursor: not-allowed;
	}
`
export default themedStyles;



