import React from 'react';
import { css } from '../utils.js';
import { Flex, Box } from 'grid-styled';
const renderTypeSizes = ({ size }) => {
  switch (size) {
    case 'large':
      return '48px';
    case 'medium':
      return '36px';
    case 'small':
      return '24px';
    default:
      return '48px';
  }
};

const StyledStatsWrapper = styled.div`
  position:relative;
  width: 100%;
  background: ${ props => props.theme.background };
`;

const StyledStats = (props) => (
  <StyledStatsWrapper {...props}>
    <Flex {...props} width={1} justifyContent="space-between" flexWrap="wrap">
      {props.children}
    </Flex>
  </StyledStatsWrapper>
);

const StatsBigNumber = styled.div`
  h1,
  h2,
  h3 {
    font-size: ${(props) => renderTypeSizes(props)};
    font-weight: 300;
    margin: 0;
    padding: 0;
  }
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
`;
const Label = styled.h4`
  font-weight: 300;
  font-family: ${ props => props.theme.font };
  opacity: 0.5;
`;
const StatsItem = styled(Box)`
  flex-grow: 1;
  display: flex;
  flex-direction: column;

  &:not(:last-of-type) {
    border-right: 1px solid ${ props => props.theme.border };
  }
`;

StatsItem.defaultProps = {
  p: 2,
};

StyledStats.BigNumber = StatsBigNumber;
StyledStats.Item = StatsItem;
StyledStats.Label = Label;

export default StyledStats;



