import React, { Component } from 'react';
import Select from 'react-select';
import {COLORWAYS, BORDERS} from './constants';
import fonts from './css_js/fonts.css.js';
import {
    any,
    contains,
    flip,
    has,
    keys,
    merge,
    prepend,
    props,
    reverse,
    startsWith,
    take,
    type,
    update,
    values
} from 'ramda';
import chroma from 'chroma-js';
import Popout from 'react-popout';
import { saveAs } from 'file-saver/FileSaver';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import * as R from 'ramda';

function ColorRoll(props) {
    const {colors, updateColor} = props;
    return (
        <div className="ColorRoll">
            {colors.map(color => (
                <div
                    className="ColorRoll--slice"
                    style={{
                        'backgroundColor': color,
                        'height': `calc(100% / ${colors.length})`
                    }}
                    onClick={() => updateColor(color)}
                />
            ))}
        </div>
    );
}

class ColorTab extends Component {
    constructor() {
        super();
        this.state = {hovered: false}
    }

    render() {
        const {
            color,
            name,
            updateColor,
            suggestedColors,
            onFocus,
            onUnfocus
        } = this.props;
        const {hovered} = this.state;
        const panelStyle = {'backgroundColor': color};
        if (hovered) {
            panelStyle.borderBottomLeftRadius = 0;
            panelStyle.borderBottomRightRadius = 0;
        }
        return (
            <div
                className="ColorTab"
            >
                <div className="ColorTab--label ColorTab--title">
                    {name}
                </div>

                <div
                    className="ColorTab--panel"
                    style={panelStyle}
                    onMouseEnter={() => {
                        console.warn('onMouseEnter');
                        this.setState({hovered: true});
                        if (!hovered) onFocus();
                    }}
                    onMouseLeave={() => {
                        console.warn('onMouseLeave');
                        this.setState({hovered: false})
                        onUnfocus();
                    }}
                >

                    <div
                        className={`ColorTab--editor ${hovered ? "": "hidden"}`}
                    >
                        <ColorRoll
                            colors={suggestedColors || []}
                            updateColor={updateColor}
                        />
                    </div>

                </div>

                <div
                    className="ColorTab--label ColorTab--footer"
                >
                    <input
                        className="ColorTab--input"
                        type="text"
                        value={color}
                        onChange={e => updateColor(e.target.value)}
                    />
                </div>

            </div>
        );
    }
}
ColorTab.defaultProps = {
    onFocus: () => {},
    onUnfocus: () => {}
}


function PageColorSettings(props) {
    const {colors, updateColor} = props;

    const suggestedColors = {
        'background': [
            'rgb(255, 255, 255)',
            'rgb(30, 30, 30)',
            '#FDF3E7' // cream
        ],
        'border': [
            'lightgrey',
            'white',
            '#6D7993'
        ],
        'text': [
            'rgb(30, 30, 30)',
            'white',
            '#6D7993',
            '#062F4F'
        ],
        'accent': [
            /*
             * taken from http://clrs.cc/ and
             * https://www.canva.com/learn/website-color-schemes/
             */
            '#001f3f', // Navy
            '#0074D9', // Blue
            '#7FDBFF', // Aqua
            '#39CCCC', // TEAL
            '#3D9970', // OLIVE
            '#2ECC40', // GREEN
            '#01FF70', // LIME
            '#FFDC00', // YELLOW
            '#FF851B', // ORANGE
            '#FF3B3F', // WATERMELON
            '#85144b', // MAROON
            '#813772', // POSY
            '#A239CA', // FUCHSIA
            '#B10DC9', // PURPLE

        ]
    }

    return (
        <div>
            {
                ['background', 'border', 'text', 'accent'].map(color => (
                    <ColorTab
                        name={color}
                        color={colors[color]}
                        updateColor={newColor => updateColor(
                            {[color]: newColor}
                        )}
                        suggestedColors={suggestedColors[color]}
                    />
                ))
            }
        </div>
    )
}

class TraceColorWay extends Component {
    constructor() {
        super();
        this.state = {}
        this.setSuggestions = this.setSuggestions.bind(this);
    }

    setSuggestions(c, i) {
        console.warn('setSuggestions');
        this.setState({[i]: [
            chroma(c).brighten(2).css(),
            chroma(c).brighten(1).css(),
            chroma(c).brighten(0.5).css(),
            c,
            chroma(c).darken(0.5).css(),
            chroma(c).darken(1).css(),
            chroma(c).darken(2).css()
        ]});
    }

    render() {
        const {colors, updateColor} = this.props;
        return (
            <div>
                {colors.map((c, i) => (
                    <ColorTab
                        name={`trace ${i+1}`}
                        color={c}
                        updateColor={newColor => updateColor(
                            update(i, newColor, colors)
                        )}
                        onFocus={() => this.setSuggestions(c, i)}
                        onUnfocus={() => this.setSuggestions(c, i)}
                        suggestedColors={this.state[i]}
                    />
                ))}
            </div>
        )
    }
}

function takeN(list, N) {
    const interval = Math.floor((list.length) / (N));
    return take(N)(list.filter((e, i) => i % interval === 0));
}

// Parse the CSS @import statement into an array of fonts
function parseGoogleFonts(atImport) {
    const regex = /(\||\=)(.*?)(\:|\||\')/g;
    // Replace `+` with ` `
    return atImport[0].match(regex).map(font => font.replace(/[\W+]/g, ' ').trim());
}

function BorderPicker(props) {
    const {onChange, options, value} = props;
    function optionRenderer(option) {
        const {value, label} = option;
        return (
            <div 
                className="BorderPicker--option"
                style={merge(
                    {
                        'padding': '5px',
                        'textAlign': 'center',
                        'borderColor': 'grey'
                    },
                    BORDERS[value])
                }
             >
                {label}
            </div>
        )
    }
    function renderValue(option) {
        return <span>{option.label}</span>;
    }
    return (
        <Select
            options={options}
            value={value}
            clearable={false}
            searchable={false}
            optionRenderer={optionRenderer}
            valueRenderer={renderValue}
            onChange={option => {
                onChange(option.value)
            }}
        />
    );
}

function FontPicker(props) {
    const {onChange, options, value} = props;
    function optionRenderer(option) {
        const {value} = option;
        return (
            <div 
                className="FontPicker--option"
                style={{
                    'font-family': value
                }}
             >
            {value}
            </div>
        )
    }
    function renderValue(option) {
        return <span>{option.value}</span>;
    }
    return (
        <Select
            options={options}
            value={value}
            clearable={false}
            optionRenderer={optionRenderer}
            valueRenderer={renderValue}
            onChange={option => {
                onChange(option.value)
            }}
        />
    );
}

function ColorWayPicker(props) {
    const {onChange, options, value} = props;
    function optionRenderer(option) {
        const {value} = option;
        return (
            <div className="ColorWayPicker--option cf">
                {JSON.parse(value).map(color => (
                    <div
                        className="ColorWayPicker--option-item"
                        style={{
                            'backgroundColor': color,
                        }}
                    />
                ))}
            </div>
        )
    }
    return (
        <Select
            options={options}
            value={JSON.stringify(value)}
            searchable={false}
            clearable={false}
            optionRenderer={optionRenderer}
            valueRenderer={optionRenderer}
            placeholder={''}
            onChange={option => {
                onChange(JSON.parse(option.value))
            }}
            closeOnSelect={false}
        />
    );
}


class App extends Component {
    constructor() {
        super();
        this.state = {
            traces: takeN(COLORWAYS.categorical, 5),
            background: 'white',
            border: 'lightgrey',
            text: '#2a3f5f',
            accent: '#119DFF'
        };
    }

    render() {
        const {traces, background, border, text, accent} = this.state;
        return (
            <div className="app">
                <Editor
                    class="Editor-panel"
                    traces={traces}
                    background={background}
                    border={border}
                    text={text}
                    accent={accent}
                    updateProps={newProps => this.setState(newProps)}
                />
            </div>
        );
    }
}


class Editor extends Component {
    constructor() {
        super();
        this.state = {
            colorways: keys(COLORWAYS).map(
                k => JSON.stringify(takeN(COLORWAYS[k], 5))
            ),
            themes: [
                {
                    background: '#F3F6FA',
                    border: '#C8D4E3',
                    text: '#506784',
                    accent: '#119DFF'
                },
                {
                    background: 'white',
                    border: 'lightgrey',
                    text: '#2a3f5f',
                    accent: '#119DFF'
                },
                {
                    background: 'rgb(40, 44, 52)',
                    border: 'rgb(59, 64, 72)',
                    text: 'rgb(171, 178, 191)',
                    accent: 'rgb(86, 175, 239)'
                },
                {
                    background: 'rgb(40, 44, 52)',
                    border: 'rgb(59, 64, 72)',
                    text: 'rgb(171, 178, 191)',
                    accent: '#FF3B3F'
                },
                {
                    background: '#FCFFF5',
                    border:     '#91AA9D',
                    text:       '#3E606F',
                    accent:     '#193441'
                },
                {
                    background: '#BDD4DE',
                    border:     '#3F5765',
                    text:       '#2B3A42',
                    accent:     '#FF530D'
                },
                {
                    background: '#FFF6E5',
                    border:     '#2185C5',
                    text:       '#3E454C',
                    accent:     '#FF7F66'
                },
                {
                    background: '#2C3E50',
                    border:     '#3498DB',
                    text:       '#ECF0F1',
                    accent:     '#E74C3C'
                }
            ],
            syncWithAccent: true
        }

        this.updateTheme = this.updateTheme.bind(this);
        this.generateCode = this.generateCode.bind(this);
    }

    componentWillReceiveProps(nextProps) {
        const {traces} = nextProps;
        const {colorways} = this.state;

        // update options
        const tracesKey = JSON.stringify(traces);
        if (!contains(tracesKey, colorways)) {
            this.setState({colorways: prepend(tracesKey, colorways)});
        }
    }

    updateTheme(colors) {
        const {updateProps} = this.props;
        const {syncWithAccent} = this.state;
        if (syncWithAccent && has('accent', colors)) {
            const c = colors.accent;
            colors.traces = [
                c,
                chroma(c).brighten(0.7).css(),
                chroma(c).brighten(1.4).css(),
                chroma(c).brighten(2.1).css(),
            ];
        }
        updateProps(colors);
    }

    generateCode() {
        const serialized =
            R.replace(
                /false/g, 'False',
                R.replace(
                    /true/g, 'True',
                    JSON.stringify(
                        R.omit(['updateProps'], this.props), null, 4
                    )
                )
            )

        return `theme = ${serialized}`;
    }

    render() {
        const {
            border_style,
            traces,
            background,
            border,
            text,
            font,
            accent,
            updateProps
        } = this.props;
        const {colorways, syncWithAccent, themes} = this.state;
        const pageColors = {background, border, text, accent};
        // TODO: 'save theme in options' checkbox
        // themes.push(pageColors);
        return (
          <div style={{'backgroundColor': '#fff'}}>
            <div className="SettingsContainer">
            <div className="Settings" style={{'min-width': keys(pageColors).length*110}}>
                <div className="cf Settings--title-container">
                    <div className="Settings--title">
                        {'Page Colors'}
                    </div>

                    <div className="ColorWayPicker--container">
                        <ColorWayPicker
                            value={values(pageColors)}
                            options={themes.map(c=>{
                                const orderedValues = props([
                                    'background',
                                    'border',
                                    'text',
                                    'accent'
                                ], c);
                                const stringified = JSON.stringify(orderedValues);
                                return {
                                    label: stringified,
                                    value: stringified,
                                }
                            })}
                            onChange={value => {
                                this.updateTheme({
                                    background: value[0],
                                    border: value[1],
                                    text: value[2],
                                    accent: value[3]
                                });
                            }}
                        />
                    </div>
                </div>
                <PageColorSettings
                    colors={pageColors}
                    updateColor={this.updateTheme}
                />
            </div>

            <div className="Settings" style={{'min-width': traces.length*110}}>

                <div className="cf Settings--title-container">
                    <div className="Settings--title">
                        {'Graph Colors'}
                    </div>
                    <div className="ColorWayPicker--container">
                        <ColorWayPicker
                            value={traces}
                            options={colorways.map(c=>({label: c, value: c}))}
                            onChange={value => updateProps({traces: value})}
                        />
                    </div>
                </div>

                <TraceColorWay
                    colors={traces}
                    updateColor={newColors => updateProps({traces: newColors})}
                />

                <div className='accentOptions'>

                    <div style={{paddingTop: '10px', float: 'left'}}>
                        <label htmlFor='reverseAccent'>Reverse</label>
                        <input
                            id='reverseAccent'
                            type='checkbox'
                            onChange={() => {
                                updateProps({traces: reverse(traces)})
                            }}
                        />
                    </div>

                    <div style={{paddingTop: '10px', float: 'left', marginLeft: '10px'}}>
                        <label htmlFor='syncAccent'>Generate from accent color</label>
                        <input
                            id='syncAccent'
                            type='checkbox'
                            checked={syncWithAccent}
                            onChange={() => this.setState({
                                syncWithAccent: !syncWithAccent
                            })}
                        />
                    </div>

                </div>

            </div>

            <div className="BordersFonts--container">
                <div className="BorderPicker--container">
                    <div className="Settings--title">
                        {'Border Styles'}
                    </div>
                    <BorderPicker
                        value={border_style['name']}
                        options={[
                            {value: 'underlined', label: 'Underlined'},
                            {value: 'threeD', label: 'threeD'},
                            {value: 'rounded', label: 'Rounded'},
                            {value: 'square', label: 'Square'},
                            {value: 'double', label: 'Double'}
                        ]}
                        onChange={value => updateProps({border_style: BORDERS[value]})}
                   />

                </div>

                <div className="FontPicker--container">
                    <div className="Settings--title">
                        {'Fonts'}
                    </div>
                    <FontPicker
                        value={font}
                        options={parseGoogleFonts(fonts).map(font => ({value: font}))}
                        onChange={value => updateProps({font: value})}
                    />
                </div>
            </div>

            <div className="Settings">
                <div className="cf Settings--title-container">
                    <div className="Settings--title">
                        {'Python Code'}
                    </div>
                </div>

                <div>
                    <button
                        id='saveTheme'
                        onClick={() => {
                            var blob = new Blob([
                                    "// Place in your app's '/assets' directory \n"
                                    + 'window.'
                                    + this.generateCode().toString()
                                ], 
                                {type: "application/javascript;charset=utf-8"});
                            saveAs(blob, "myTheme.js");
                        }}
                    >{'Save'}</button>

                    <CopyToClipboard 
                        text={this.generateCode().toString()}
                        onCopy={() => {
                            this.setState({copied: true})
                            setTimeout(() => this.setState({copied: false}), 2000);
                        }}
                    >
                        <button>{ this.state.copied ? 'Copied!' : 'Copy' }</button>
                    </CopyToClipboard>
                </div>

                <pre style={{
                    'borderLeft': 'thin lightgrey solid',
                    'paddingLeft': 10
                }}>{this.generateCode()}</pre>

            </div>
            </div>
          </div>
        );
    }
}

function copyStyles(sourceDoc, targetDoc, filter=false) {
  Array.from(sourceDoc.styleSheets).forEach(styleSheet => {
    if (filter &&
            type(styleSheet.href) === 'String' &&
            !any(flip(contains)(styleSheet.href), filter)) {
        return;
    }
    console.warn(styleSheet.href);
    if (styleSheet && styleSheet.cssRules) { // for <style> elements
      const newStyleEl = sourceDoc.createElement('style');

      /* FIXME: the popout theme editor will break if 'app.css.config.serve_locally`
         is False in usage.py. CORS issue in Chrome >= 6+
         Error: `Uncaught DOMException: Failed to read the 'cssRules' property from
         'CSSStyleSheet': Cannot access rules`
      */
      Array.from(styleSheet.cssRules).forEach(cssRule => {
        // write the text of each rule into the body of the style element
        newStyleEl.appendChild(sourceDoc.createTextNode(cssRule.cssText));
      });

      targetDoc.head.appendChild(newStyleEl);
    } else if (styleSheet.href) { // for <link> elements loading CSS from a URL
      const newLinkEl = sourceDoc.createElement('link');

      newLinkEl.rel = 'stylesheet';
      newLinkEl.href = styleSheet.href;
      targetDoc.head.appendChild(newLinkEl);
    }
  });
}


function EditorWithPopout(props) {
    return (
        <div className="Editor">
            <Popout
                title="Dash Theme Editor"
                options={{
                    width: 700,
                    height: 800,
                    toolbar: 0,
                    location: 0,
                    titlebar: 0,
                    left: 400
                }}
            >
                {
                    popoutWindow => {
                        copyStyles(
                            window.document,
                            popoutWindow.document,
                            ['/editor.css', '/react-select', '/react-virtualized']
                        );
                        return (
                            <Editor {...props}/>
                        );
                    }
                }
            </Popout>
        </div>
    );
}

export default Editor;
export {EditorWithPopout};



