/**
 * Grid Component
 */

import React, {Component} from 'react';
import PropTypes from 'prop-types';

import { WidthProvider, Responsive } from "react-grid-layout";

import Card from './Card.react.js';

const ResponsiveReactGridLayout = WidthProvider(Responsive);
const originalLayouts = getFromLS("layouts") || {};
/**
 *  * This layout demonstrates how to sync to localstorage.
 *   */
export default class Grid extends Component {

    defaultProps() {
      return {
          className: "layout",
          cols: 12,
          rowHeight: 100,
          onLayoutChange: () => {},
      }
    }

  constructor(props) {
      super(props);
  
      this.state = {
            layouts: JSON.parse(JSON.stringify(originalLayouts))
          };
  
      // this.onLayoutChange = this.onLayoutChange.bind(this);
      this.resetLayout = this.resetLayout.bind(this);
    }

  resetLayout() {
      this.setState({
            layouts: {}
          });
    }

// TODO: This needs debouncing for performance
  onResize() {
    console.log("card resize detected");
    // TODO: This needs a shim for IE
    window.dispatchEvent(new Event('resize'));
  }

//  onLayoutChange(layout, layouts) {
//      /*eslint no-console: 0*/
//      saveToLS("layouts", layouts);
//      this.setState({ layouts });
//      // this.props.onLayoutChange(layout); // updates status display
//  }

    render() {
        let error = null;

        const child_cards = React.Children.map(this.props.children, (child) => (child.props.children ));

        // map out cards to wrap them with divs with keys
        const cards = React.Children.map(child_cards, (c, i) => {
            if (c.type !== Card) {
                error = new Error('`Grid children should be of type `Card`.'); 
            }
            let { children, width, height, x, y, minWidth, minHeight } = c.props
            return (
                <div key={i} data-grid={{ w: width, h: height, x: x, y: y, minW: minWidth, minH: minHeight }}>
                    <div className="draggable-handle">✢</div>
                    {c}
                </div>
            )
        })

        return (
            <div classname="dashboard-container">
              <span className='reset-layout' style={{'cursor': 'not-allowed'}} onClick={this.resetLayout}>▦ reset</span>
              <ResponsiveReactGridLayout
                className="dashboard-layout"
                draggableCancel="input,textarea,.plot-container"
                compactType='horizontal'
                preventCollision={true}
                isDraggable={true}
                isResizable={true}
                autoSize={true}
                onResize={this.onResize}
                cols={{ lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 }}
                rowHeight={120}
                layouts={this.state.layouts}
                // onLayoutChange={this.onLayoutChange}
                {...this.props}
              >
                {cards}
              </ResponsiveReactGridLayout>
            </div>
          );
    }
}

function getFromLS(key) {
  let ls = {};
  if (global.localStorage) {
      try {
            ls = JSON.parse(global.localStorage.getItem("rgl-8")) || {};
          } catch (e) {
                /*Ignore*/
              }
    }
  return ls[key];
}

function saveToLS(key, value) {
  if (global.localStorage) {
      global.localStorage.setItem(
            "rgl-8",
            JSON.stringify({
                    [key]: value
                  })
          );
    }
}

Grid.propTypes = {
  id: PropTypes.string,
  children: PropTypes.node
};





