/* global Plotly:true */
import React, {Component, PropTypes} from 'react';
import * as R                        from 'ramda';
import chroma                        from 'chroma-js';
import Draggable                     from 'react-draggable';
import transformTheme                from '../transformTheme';
import ColorTheme                    from '../ColorTheme.react';
import RadioItems                    from '../RadioItems.react';
import Editor, {EditorWithPopout}    from '../ThemeEditor.react';
import styled, { ThemeProvider, injectGlobal }      from 'styled-components';
import {BORDERS, THEMES}             from '../constants'

/* These all export one const template literal as 'default' */
import style_fonts              from '../css_js/fonts.css.js';

import style_slider             from '../css_js/var-rc-slider@6.1.2.css.js';
import style_dates              from '../css_js/var-react-dates@12.3.0.css.js';
import style_select             from '../css_js/var-react-select@1.0.0-rc.3.css.js';
import style_virtualized_select from '../css_js/var-react-virtualized-select@3.1.0.css.js';
import style_base               from '../css_js/var-base.css.js';

import style_base_override        from '../css_js/override-base.css.js';
import style_select_override      from '../css_js/override-react-select@1.0.0-rc.3.css.js'
import style_dates_override       from '../css_js/override-react-dates@12.3.0.css.js'
import style_virtualized_override from '../css_js/override-react-virtualized-select@3.1.0.css.js'

/*
 * event polyfill for IE
 * https://developer.mozilla.org/en-US/docs/Web/API/CustomEvent/CustomEvent
 */
function CustomEvent (event, params) {
    params = params || {
        bubbles: false,
        cancelable: false,
        detail: undefined
    };
    var evt = document.createEvent('CustomEvent');
    evt.initCustomEvent(
        event, params.bubbles, params.cancelable, params.detail);
    return evt;
}
CustomEvent.prototype = window.Event.prototype;


// NOTE: Order does not affect specificity
const DashThemeProvider = styled.div`
	${style_select_override}
	${style_select}
	${style_slider}
	${style_dates}
	${style_virtualized_select}
	${style_base}
	${style_dates_override}
	${style_base_override}
	${style_fonts}
`;



const EditorThemeProvider = styled.div`
	${style_base}
	${style_base_override}
`;

class App extends Component {
    constructor(props) {
        super();
        const defaultTheme = R.defaultTo(window.theme || THEMES.light);
        this.state = {
            display_editor: false,
            theme: defaultTheme(props.theme)
        };
    }

    render() {
        const {children, show_editor} = this.props;
        const {theme, display_editor} = this.state;
        const transformedTheme = transformTheme(theme);
        window.dashTheme = transformedTheme;
-       // Base overrides
        injectGlobal`
            body {
                color: ${theme.text};
                background-color: ${theme.background};
                font-family: ${theme.font};
                // font cache busting hack
                border-image-width: ${Math.random()};
                margin: 0;
                --background: ${theme.background};
                --border: ${theme.border};
                --accent: ${theme.accent};
                --accent-faded: ${chroma(theme.accent).alpha(0.5).css()};
                --trace-0: ${theme.traces[0]};
                --trace-1: ${theme.traces[1]};
                --trace-2: ${theme.traces[2]};
                --trace-3: ${theme.traces[3]};
                --trace-4: ${theme.traces[4]};
                --trace-5: ${theme.traces[5]};
                --trace-6: ${theme.traces[6]};
            }
          `;

		const themeConstants = {
			highlight: '#ffe8bc', // not accessible
			highlightSecondary: '#007a87', // not accessible
			overlay: 'rgba(0, 0, 0, 0.3)'
		};
        return (
            <div>
                {show_editor ? (
                    <button
                        onClick={
                            () => this.setState({display_editor: !display_editor
                        })}
                        style={{'position': 'fixed', 'bottom': 10, 'left': 10, 'zIndex': 1000}}
                    >
                        {'Edit Theme'}
                    </button>
                ) : null}

                <ThemeProvider theme={theme}>
                    <DashThemeProvider themeConstants={themeConstants}>
                        {children}
                    </DashThemeProvider>
                </ThemeProvider>

                {display_editor ? (
                    <EditorWithPopout
                        traces={theme.traces}
                        background={theme.background}
                        border={theme.border}
                        text={theme.text}
                        border_style={theme.border_style}
                        font={theme.font}
                        accent={theme.accent}
                        backgroundSecondary={theme.backgroundSecondary}
                        disabledText={theme.disabledText}
                        disabledBackground={theme.disabledBackground}
                        buttonShadow={theme.buttonShadow}
                        disabled={theme.disabled}
                        textSecondary={theme.textSecondary}
                        borderRadiusPrimary={theme.borderRadiusPrimary}

                        updateProps={themeUpdate => {
                            console.warn(themeUpdate);
                            const newTheme = R.merge(this.state.theme, themeUpdate)
                            this.setState({theme: newTheme});
                            window.dashTheme = transformTheme(newTheme);
                            window.dispatchEvent(new CustomEvent('dash-theme-update'));
                        }}
                    />
                ) : null}

            </div>
        );
    }

}

App.propTypes = {
    theme: PropTypes.object,
    children: PropTypes.object,
    show_editor: PropTypes.bool
}

export default App;



