import { css } from '../utils.js';
import chroma from 'chroma-js';

const themedStyles = css`
    .rc-slider-rail {
      background-color: ${props => props.theme.backgroundSecondary};
    }

    .rc-slider-track {
      background-color: ${props => props.theme.accent};
    }

    .rc-slider-handle {
      border-color: ${props => props.theme.accent};
      background-color: ${props => props.theme.background};
    }

    .rc-slider-handle:hover {
      border-color: ${props => chroma(props.theme.accent).brighten().css()};
    }

    .rc-slider-handle-active:active {
      border-color: ${props => chroma(props.theme.accent).brighten().css()};
      box-shadow: ${props => chroma(props.theme.accent).brighten().css()}; 
    }

    .rc-slider-mark-text {
      color: ${props => props.theme.textSecondary};
    }

    .rc-slider-mark-text-active {
      color: ${props => props.theme.text};
    }

    .rc-slider-step {
      background-color: transparent;
    }

    .rc-slider-dot {
      border-color: ${props => props.theme.backgroundSecondary};
      background-color: ${props => props.theme.background};
    }

    .rc-slider-dot-active {
      border-color: ${props => props.theme.accent};
    }

    .rc-slider-disabled {
      background-color: ${props => props.theme.backgroundSecondary};
    }

    .rc-slider-disabled .rc-slider-track {
      background-color: ${props => props.theme.disabled};
    }

    .rc-slider-disabled .rc-slider-handle,
    .rc-slider-disabled .rc-slider-dot {
      border-color: ${props => props.theme.disabled};
      background-color: ${props => props.theme.background};
    }

    .rc-slider-tooltip-inner {
      color: ${props => props.theme.background};
      background-color: ${props => props.theme.backgroundSecondary};
      box-shadow: ${props => chroma(props.theme.accent).brighten().css()}; 
    }

    .rc-slider-tooltip-arrow {
      border-color: transparent;
    }

    .rc-slider-tooltip-placement-top .rc-slider-tooltip-arrow {
      border-top-color: ${props => props.theme.backgroundSecondary};
    }
`
export default themedStyles;



