import React, {Component} from 'react';
import StyledStats from '../styles/stats.styled';
import PropTypes from 'prop-types';
/**
 * Stats
 * This component can render out stat items with a large number and a label component
 */

const renderTypeComponent = (size, children) => {
  switch (size) {
    case 'large':
      return <h1>{children}</h1>;
    case 'medium':
      return <h2>{children}</h2>;
    case 'small':
      return <h3>{children}</h3>;
    default:
      return <h1>{children}</h1>;
  }
};

export default class Stat extends Component {
    constructor(props) {
        super(props);
    }
    
    render() {
        const { value, size, label } = this.props;
        return (
            <StyledStats.Item>
                <StyledStats.BigNumber>
                    {renderTypeComponent(size, value)}
                </StyledStats.BigNumber>
                <StyledStats.Label>{label}</StyledStats.Label>
            </StyledStats.Item>
        )
    }
}

Stat.propTypes = {
    value: PropTypes.number,
    size: PropTypes.string,
    label: PropTypes.string
}



