    // .Select-option
    '--color-4-react-select': theme.text,

    // .Select-option.is-disabled
    '--color-5-react-select': theme.disabledText,

    // .Select-noresults
    '--color-6-react-select': theme.textSecondary,

    // .Select--multi .Select-value-icon:hover, .Select--multi .Select-value-icon:focus
    '--color-7-react-select': theme.accent,

    // override
    '--color-8-react-select': theme.text,

    /* react-virtualized-select */
    // .VirtualizedSelectFocusedOption
    '--background-color-1-react-virtualized-select': theme.background,

    // override
