/* eslint-disable react/prop-types */
import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { css } from '../utils.js';
import chroma from 'chroma-js';

const StyledMenuItem = styled.li`

  border-color: ${ props => props.theme.border };

  ${ props => objectToStyles(props.theme.border_style) };

  &:hover {
    background-color: ${ props => 
        chroma(props.theme.background).luminance() >= 0.5 ?
        chroma(props.theme.background).darken() :
        chroma(props.theme.background).brighten()
    };
  }

`;

/**
 * A leveled menu.
 */
export default class Menu extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const { id, children, props } = this.props;


        const links = React.Children.map(children, (child) => (child.props.children ));

        console.log("links :", links);

        const menuItems = links.map((child, i) => {
            return (
                <StyledMenuItem>
                    <a href={child.props.href}>
                        <div className="menu-item">
                            {child}
                        </div>
                    </a>
                </StyledMenuItem>
            )
        });

        console.log("menuItems :", menuItems);

        return (
            <div className="layout-menu">
                <ul>
                    {menuItems}
                </ul>
            </div>
        )
    }
}

Menu.propTypes = {
    id: PropTypes.string,
    children: PropTypes.node,

    /**
     * Dash-assigned callback that gets fired when the value changes.
     */
    setProps: PropTypes.any,
};



