import { css } from '../utils.js';

const themed = key => props => props.theme[key]

const themedStyles = css`
    body {
      color: ${props => props.theme.text};
      background-color: ${props => props.theme.background};
    }
    blockquote {
      border-left-color: ${props => props.theme.border};
    }
    a {
      color: ${props => props.theme.accent}; }
    a:hover {
      color: ${props => props.theme.accent}; }
    .button,
    button,
    input[type="submit"],
    input[type="reset"],
    input[type="button"] {
      color            : ${props => props.theme.traces[1]};
      background-color : ${props => props.theme.background};
      border-color     : ${props => props.theme.border};
      transition       : all 0.2s ease;
      box-shadow       : 0 4px 6px ${props => props.theme.buttonShadow};
    }

    .button:active,
    button:active,
    input[type="submit"]:active,
    input[type="reset"]:active,
    input[type="button"]:active {
        box-shadow: none;
    }
    .button:hover:enabled,
    button:hover:enabled,
    input[type="submit"]:hover:enabled,
    input[type="reset"]:hover:enabled,
    input[type="button"]:hover:enabled,
    .button:focus,
    button:focus,
    input[type="submit"]:focus,
    input[type="reset"]:focus,
    input[type="button"]:focus {
      color: ${props => props.theme.accent};
      border-color: ${props => props.theme.accent};
      outline-color: 0; }
    .button.button-primary,
    button.button-primary,
    input[type="submit"].button-primary,
    input[type="reset"].button-primary,
    input[type="button"].button-primary {
      color: ${props => props.theme.background};
      background-color: ${props => props.theme.traces[1]};
      border-color: ${props => props.theme.traces[1]}; }
    .button.button-primary:hover:enabled,
    button.button-primary:hover:enabled,
    input[type="submit"].button-primary:hover:enabled,
    input[type="reset"].button-primary:hover:enabled,
    input[type="button"].button-primary:hover:enabled,
    .button.button-primary:focus,
    button.button-primary:focus,
    input[type="submit"].button-primary:focus,
    input[type="reset"].button-primary:focus,
    input[type="button"].button-primary:focus {
      color: ${props => props.theme.traces[1]};
      background-color: ${props => props.theme.background};
      border-color: ${props => props.theme.traces[1]}; }
    input:not([type]),
    input[type="email"],
    input[type="number"],
    input[type="search"],
    input[type="text"],
    input[type="tel"],
    input[type="url"],
    input[type="password"],
    textarea,
    select {
      background-color: ${props => props.theme.background};
      border-color: ${props => props.theme.border};}
    input[type="email"]:focus,
    input[type="number"]:focus,
    input[type="search"]:focus,
    input[type="text"]:focus,
    input[type="tel"]:focus,
    input[type="url"]:focus,
    input[type="password"]:focus,
    textarea:focus,
    select:focus,
    input[type="email"]:hover:enabled,
    input[type="number"]:hover:enabled,
    input[type="search"]:hover:enabled,
    input[type="text"]:hover:enabled,
    input[type="tel"]:hover:enabled,
    input[type="url"]:hover:enabled,
    input[type="password"]:hover:enabled,
    textarea:hover:enabled,
    select:hover:enabled {
      border-color: ${props => props.theme.accent};
      outline-color: 0; }
    th,
    td {
      border-bottom-color: ${props => props.theme.border}; }
    hr {
      border-top-color: ${props => props.theme.border}; }

    // CHECKBOXES and RADIO BUTTONS

    input[type=checkbox],
    input[type=radio]   {
        border: 1px solid ${props => props.theme.border};
        color: ${props => props.theme.accent};
        width: 20px;
        height: 20px;
        outline: 0;
        padding: 0;
        float: left;
        -webkit-appearance: none;
        -moz-appearance: none;
        margin-right: 5px;
    }

    input[type=radio] {
        border-radius: 50%;
        position: relative;
    }

    input[type=radio]:checked:before {
        content: "";
        background-color: ${props => props.theme.accent};
        float: left;
        height: 10px;
        width: 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    input[type=checkbox]:checked:before {
        content: "✔";
        float: left;
        width: 20px;
        height: 20px;
        line-height: 20px;
        text-align: center;
    }

    input[type=radio]:checked:before {
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
    }

`
export default themedStyles;



