import React, {Component} from 'react';
import PropTypes from 'prop-types';
import StyledStats from '../styles/stats.styled';
import Stat from './Stat.react';
/**
 * Stat
 * This component displays a single stat
 */

export default class Stats extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const { id, ...props } = this.props;
        let error = null;

        // cast children to array
        const children = React.Children.map(this.props.children, (child) => (child.props.children ));

        const renderStatItems = children.map((child, i) => {
            const { value, size, label } = child.props;
            return (<Stat key={i} value={value} size={size} label={label} />)
        });
        return <StyledStats {...props}>{renderStatItems}</StyledStats>;

    }
}

Stats.propTypes = {
    id: PropTypes.string,
    children: PropTypes.node
}



