import { css } from '../utils.js';
import * as R          from 'ramda';

const themedStyles = css`
	&& .button,
	&& button,
	&& input[type="submit"],
	&& input[type="reset"],
	&& input[type="button"] {${props => objectToStyles(props.theme.border_style)}}

	&& input:not([type]),
	&& input[type="email"],
	&& input[type="number"],
	&& input[type="search"],
	&& input[type="text"],
	&& input[type="tel"],
	&& input[type="url"],
	&& input[type="password"],
	&& textarea,
	&& select {
		${props => objectToStyles(props.theme.border_style)}
	}

	&& input:not([type]):focus,
	&& input[type="email"]:focus,
	&& input[type="number"]:focus,
	&& input[type="search"]:focus,
	&& input[type="text"]:focus,
	&& input[type="tel"]:focus,
	&& input[type="url"]:focus,
	&& input[type="password"]:focus {
		${props => R.path(['theme', 'border_style', 'inputFocus'], props)}
	}

	&& input:disabled {
		color: ${props => props.theme.disabledText};
		background-color: ${props => props.theme.disabledBackground};
		border-bottom: none;
	}

	&& label {
		margin-right: 5px;
	}
`
export default themedStyles;



