/**
 * Card Component
 *
 * Elements:
 * Header (overflow actions?)
 * Content
 * Footer
 *
 * Things I want it to be able to accommodate
 * - control aspect ratio
 *   - eg be square
 * - display in different ways
 *   - eg no shadow (different based off of theme?)
 */
import React from 'react';
import PropTypes from 'prop-types';

/**
 * Card (root container)
 */
const Card = ({ children, ...rest }) => (
    <div>
        {children}
    </div>
);

Card.defaultProps = {
  key: '1',
  width:  2,
  y:      0,
  minWidth: 2,
  minHeight: 3
};

Card.propTypes = {
  key: PropTypes.string,
  id: PropTypes.string,
  width: PropTypes.number,
  height: PropTypes.number,
  x:      PropTypes.number,
  y:      PropTypes.number,
  minWidth: PropTypes.number,
  minHeight: PropTypes.number,
  children: PropTypes.node
};

export default Card;



